    top: 0;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(50%, rgba(0, 0, 0, 0.1)), color-stop(68%, rgba(0, 0, 0, 0.3)), color-stop(80%, rgba(0, 0, 0, 0.8)), to(black));
